html, body {
    margin: 0;
    padding: 0;
    min-height: 100vh;
}

content {
    display: contents;
    width: 100%;
    height: 100%;
}

canvas {
    position: absolute;
    top: 0;
    left: 0;
    background: radial-gradient(circle, #016ead 0%,#000000 100%);
    cursor: none;
}

h1 {
    position: inherit;
    margin: 0;
    text-align: center;
    font-size: 100px;
    color:#fefdd1;
    text-transform: uppercase;
    font-family: Arial, Helvetica, sans-serif;
    each(range(10), {
        text-shadow+: (@value * 1px) (@value * -1px)#86642b;
    });
}
